<template>
  <div class="canvas">
    <svg
      :width="viewportWidth"
      :height="viewportHeight"
      :viewBox="`${viewboxPositionX} ${viewboxPositionY} ${zoomX} ${zoomY}`"
      :class="{ 'cursor-grabbing': this.moveStart }"
      @wheel="onScroll"
      @mousedown="onDown"
      @touchstart="onDown"
      @mouseup="onUp"
      @touchend="onUp"
      @mousemove="onMove"
      @touchmove="onMove"
      @mouseleave="onUp"
      xmlns="http://www.w3.org/2000/svg"
    >
      <foreignObject x="100" y="100" width="1000" height="1000">
        <div
          xmlns="http://www.w3.org/1999/xhtml"
          class="grid"
          :class="{ unclickable: this.moveStart }"
        >
          <HelloWorld v-for="item in test" :key="item"></HelloWorld>
        </div>
      </foreignObject>
    </svg>
  </div>
</template>

<script>
import HelloWorld from "@/components/HelloWorld.vue";

export default {
  components: {
    HelloWorld,
  },
  data() {
    return {
      viewportWidth: 0,
      viewportHeight: 0,
      //
      viewboxPositionX: 0,
      viewboxPositionY: 0,
      //
      zoom: 5,
      zoomRatio: 0.2,
      zoomX: 3000,
      zoomY: 3000,
      //
      moveStart: false,
      moveStartX: 0,
      moveStartY: 0,
      moveCurrentX: 0,
      moveCurrentY: 0,
      moveViewportX: 0,
      moveViewportY: 0,
      //
      test: [1, 2, 3, 4, 5, 6],
    };
  },
  methods: {
    onScroll(e) {
      if (e.deltaY > 0) {
        console.log('+')
        this.zoom = this.zoom + 1;
        this.onZoom();
      } else {
        console.log('-')
        this.zoom = this.zoom - 1;
        this.onZoom();
      }
    },
    onZoom() {
      let zf = this.zoom * this.zoomRatio;
      let zX = zf * this.viewportWidth;
      let zY = zf * this.viewportHeight;
      let zMinX = this.viewportWidth / 10;
      let zMinY = this.viewportHeight / 10;

      if (zX <= 100) {
        this.zoomX = zMinX;
      } else {
        this.zoomX = zX;
      }

      if (zY <= 100) {
        this.zoomY = zMinY;
      } else {
        this.zoomY = zY;
      }
    },
    onDown(e) {
      this.moveStartX = e.clientX;
      this.moveStartY = e.clientY;
      this.moveViewportX = this.viewboxPositionX;
      this.moveViewportY = this.viewboxPositionY;
      this.moveStart = true;
    },
    onUp() {
      this.moveStartX = 0;
      this.moveStartY = 0;
      this.moveViewportX = 0;
      this.moveViewportY = 0;
      this.moveStart = false;
    },
    onMove(e) {
      this.moveCurrentX = e.clientX;
      this.moveCurrentY = e.clientY;

      if (this.moveStart) {
        let distanceX = this.moveCurrentX - this.moveStartX;
        let distanceY = this.moveCurrentY - this.moveStartY;

        if (distanceX > 0) {
          this.viewboxPositionX = this.moveViewportX - Math.abs(distanceX);
        } else {
          this.viewboxPositionX = this.moveViewportX + Math.abs(distanceX);
        }

        if (distanceY > 0) {
          this.viewboxPositionY = this.moveViewportY - Math.abs(distanceY);
        } else {
          this.viewboxPositionY = this.moveViewportY + Math.abs(distanceY);
        }
      }
    },
  },
  mounted() {
    this.viewportWidth = window.innerWidth;
    this.viewportHeight = window.innerHeight;
    //
    // this.zoomX = this.viewportWidth;
    // this.zoomY = this.viewportHeight;
    this.onZoom();
  },
};
</script>
